@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,300;1,700&family=Poppins:wght@300;400;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: "Lato", sans-serif;
  /* max-width: 2400px; */
}
.text-font {
  font-family: "Poppins", sans-serif;
}

.p {
  padding: 8px;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 2%;
  color: #1a1941;
  margin: 8px;
  border: 1px solid #ffffff;
  border-radius: 8px;

  cursor: pointer;
}
.previousBttn {
  left: 0px;
  top: 0px;
  border-radius: 6px;
  padding: 10px;
  background-color: #1a1941;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
}
.nextBttn {
  left: 0px;
  top: 0px;
  border-radius: 6px;
  padding: 10px;
  background-color: #1a1941;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
}
.paginationDisabled a {
  background-color: #efefef;
  color: #8b8b8b;
}
.paginationActive a {
  /* background-color:blue; */
  background: rgba(255, 255, 255, 1);
}
.hide::-webkit-scrollbar {
  display: none;
}
.hide {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.swiper {
  margin-left: auto;
  margin-right: auto;
  position: initial !important;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.mySwiper .swiper-pagination {
  position: relative;
  text-align: center;
  transition: 0.3s opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10;
  margin-top: 55px;
}
.mySwiper .swiper-pagination-bullet {
  width: 24px;
  height: 4px;
  border-radius: 0;
}
.mySwiper .swiper-pagination-bullet-active {
  background: #1a1941;
}

.mySwiper .swiper-initialized {
  position: initial;
}

.mySwiper .swiper-wrapper {
  position: initial;
}

.mySwiper .swiper-button-next {
  top: 5rem;
  width: 44px;
  height: 44px;
  background-color: #1a1941;
  right: -2px;
  border-radius: 4px;
}
.mySwiper .swiper-button-next::after {
  font-size: 20px;
  color: white;
}
.mySwiper .swiper-button-prev {
  top: 5rem;
  right: 68px;
  width: 44px;
  height: 44px;
  background-color: #1a1941;
  border-radius: 4px;
  left: auto;
}
.mySwiper .swiper-button-prev::after {
  font-size: 20px;
  color: white;
}



.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
background: rgba(217, 217, 217, 1);
opacity: initial !important;
}

.with:hover{
  background: rgba(161, 158, 249, 1);
  opacity: 65%;
  color: #181818;
  
}
.ant-upload{
  width: 341px !important;
  height: 218px !important;
  margin-left: auto;
  margin-right: auto;
}
.ant-upload-list-item{
  width: 341px !important;
   margin-left: auto;
  margin-right: auto;
}
